  font-family: 'line-awesome';
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@mixin makeIcon($arg, $val) {
  .la-#{$arg}:before {
    content: $val;
    font-size: 2.6rem;
    vertical-align: sub;
  }
}

